@media (min-width: 320px) {
  .nav-container {
    /* Positioning */
    /* padding: 0.5rem 5rem; */
    position: fixed;

    height: 40px;
    width: 100vw;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    /* Other Styling */
    background: linear-gradient(#555e6d, #313332);
    overflow: hidden;
  }
